@import url("../modal-basic.css");



.ch-modal {
    width: 95%;
    height: 95%;
    top: 1%;
    left: 1%;
}


.ch-dial-box{
    width: 95%;
    margin-left: calc(5% / 2);
    height: 80%;
    background-color: #3f3f3fbd;
}

.ch-list{
    width: 95%;
    margin-top: 1.5%;
    height: 11%;
    background-color: #3f3f3fbd;
    margin-left: calc(5% / 2);
    overflow-x: auto; /* Permite el desplazamiento horizontal */
    overflow-y: hidden; /* Permite el desplazamiento horizontal */
    white-space: nowrap; /* Asegura que los elementos se alineen en una sola línea */
}


.chat-list-g{
    display: inline-block; 
    margin-left: .5%; /* Espacio entre los items */
    text-align: center; /* Centra el texto horizontalmente */
    height: 95%;
    width:  100px;
    border: 1px solid red;
    border-radius: 50%;
    background-color: transparent; /* Color de fondo de los items */
    background-size: cover;
    background-position: center;
}

/* MOB */
@media (max-width: 769px) {
    .ch-modal {
        width: 90%;
        height: 85%;
        top: 3%;
        left: 2%;
    }

    .ch-list{
        width: 95%;
        margin-top: 2%;
        height: 13%;
        background-color: #3f3f3fbd;
        margin-left: calc(5% / 2);
    }
    .container::-webkit-scrollbar {
        width: 8px; /* Ancho de la barra de desplazamiento */
        height: 8px;
    }
    
    .container::-webkit-scrollbar-thumb {
        background-color: #888; /* Color de la barra de desplazamiento */
        border-radius: 10px; /* Bordes redondeados de la barra de desplazamiento */
    }
    
    .container::-webkit-scrollbar-thumb:hover {
        background-color: #555; /* Color al pasar el ratón sobre la barra */
    }
    
    
}

/* DESK */
@media (min-width: 769px) {
    .chat-list-g{
        cursor: pointer;
        filter: brightness(50%);
        transition: filter .3s;
    }

    .chat-list-g:hover{
        cursor: pointer;
        filter: brightness(100%);
    }
}

